import content from "./translatedContent.json"

let attribute = null;


export const setElementKey = (key) => {
    attribute = key;
};


export const translateContent = (code) => {
    let elements = document.querySelectorAll(`[${attribute}]`);

    if(!elements.length){
        return;
    }

    elements.forEach((element)=>{
        console.log(element);
        let contentKey = element.getAttribute(attribute);

        if(!content[contentKey] || !content[contentKey][code]){
            return;
        }

        if(["input", "select", "textarea"].includes(element.tagName.toLowerCase()) ){
            
        } else {
            element.innerHTML = content[contentKey][code];
        }



    });


};
